.header{
    width: 100%;
    height: 9vh;
    background: white;
    padding: 0% 3%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header .logo{
    /* height: 60%; */
    height: 5.5vh;
    max-height: 41px;
}

.header .user-icon{
    cursor: pointer;
}

.header .user-icon img{
    width: 100%;
    border-radius: 50%;
    object-fit: contain;
    display: inline-block;
    vertical-align: middle;
    object-fit: cover;
    width: 40px;
    height: 40px;
}

.header .mobile-logo{
    display: none;
}

.header .mobile-menu-icon{
    display: none;
}

.header .header-right-side{
    display: flex;
    align-items: center;
}


@media (min-width: 1000px) and (max-width: 1024px) {
    .leftmenu_bottom{
        height: 9vh!important;
    }
}

@media (min-width: 768px) and (max-width: 1000px) {
    .header{
        height: 7vh;
    }
    .leftmenu_bottom{
        height: 8vh!important;
    }
}

@media (max-device-width: 480px) {
    .header{
        padding: 0% 7%;
        padding-top: env(safe-area-inset-top); 
        height: calc(9vh + env(safe-area-inset-top));
        box-shadow: 0px 0.5px 0px rgba(0, 0, 0, 0.3);
        /* position: fixed; */
        z-index: 2;
    }
    .header .logo{
        display: none;
    }
    .header .logo-mobile-icon{
        width: 17%;
    }
    .header .mobile-logo{
        display: flex;
        align-items: center;
        text-decoration: none;

    }
    .header .mobile-logo span{
        font-size: 18px;
        color: #252F42;
        font-weight: 800;
        margin-left: 10px;
    }
    .header .user-icon{
        /* display: none; */
    }
    .header .mobile-menu-icon{
        display: flex;
        flex: 1;
        /* position: absolute;
        left: 7%; */
    }
    .header .header-right-side {
        flex: 1;
        justify-content: flex-end;
    }
}