.auth {
    font-family: Montserrat;
    font-style: normal;
}

.auth .header {
    width: 100%;
    height: 9vh;
    background: white;
    padding: 0% 6%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.auth .header img {
    height: 5.5vh;
}

.auth .header a {
    text-decoration: none;
    margin-left: 1%;
}

.auth .header span {
    color: #0F1B28;
    /* font-size: 1.3em; */
    font-size: 20px;
    font-weight: 800;
}

.auth h1 {
    margin-bottom: 9vh;
    font-weight: 700;
    font-size: 45px;
    line-height: 78px;
    color: #151B26;
    display: flex;
    align-items: center;
    max-width: 65%;
}

.auth .page-content {
    box-sizing: border-box;
    padding: 2% 6%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.auth .page-content .illustration {
    width: 28%;
    margin-top: 5vh;
    display: block;
    margin-right: 5%;
}

.auth .page-content .illustration.second {
    margin-top: 14vh;
    width: 31%;
}

.auth form {
    width: 40%;
}

.auth form a.link {
    color: #1A73E8;
    font-size: 14px;
    text-decoration: none;
    margin-top: -3vh;
    display: block;
}

.auth form .input_block_section {
    margin-bottom: 40px;
}

.auth form .input_block_section span {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #151B26;
    display: block;
    margin-bottom: 13px;
}

.auth form .input_block_section span.info {
    font-size: 12px;
    color: #686868;
    margin-top: -11px;
}

.auth form .input_block_section input {
    font-weight: 500;
    font-size: 21px;
    line-height: 28px;
    color: #686868;
    background: none;
    border: none;
    border-bottom: 1px solid #A3A3A3;
    padding-bottom: 14px;
    font-family: Montserrat;
    width: 100%;
}

.auth form .input_block_section input::placeholder {
    color: #B0B0B0;
}

.auth form .input_block_section input:focus {
    border-bottom: 1px solid #3170E9;
}

.auth form .actions {
    margin-top: 5vh;
}

.auth form .actions button {
    background: linear-gradient(45deg, #4937E8 0%, #2A82E9 100%), #C4C4C4;
    border-radius: 13px;
    border: 0;
    padding: 10px 25px;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;
    cursor: pointer;
    min-width: 130px;
    transition: background 1s ease-out;
    font-family: Montserrat;
    margin-right: 4%;
}

.auth form .actions button.reg {
    animation: smoothContent 0.8s;
}

.auth form .actions a {
    color: #1A73E8;
    font-size: 14px;
}

.auth .message {
    width: 100%;
    height: 4vh;
    /* background: black; */
    background: #F00808;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    display: none;
    animation: goUp 0.8s;
}

.auth .message.s {
    background:#1a73e8;
}

.auth .confirm-info{
    color: #717171;
    font-size: 13px;
    margin-bottom: 6vh;
}

.auth .confirm-info a{
    color: #1A73E8;
    text-decoration: none;
}

@keyframes goUp {
    from {
        transform: translateY(4vh);
        opacity: 0;
    }
    to {
        transform: translateY(0px);
        opacity: 1;
    }
}

@keyframes smoothContent {
    from {
        transform: scale(0.8);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

@media (max-device-width: 480px) {
    input {
        -webkit-appearance: none;
        border-radius: 0;
      }
    .auth{
        background: white;
        height: 100vh;
    }
    .auth .header {
        /* box-shadow: 0px 0.5px 0px rgba(0, 0, 0, 0.3); */
        border-bottom: 0.5px rgba(0, 0, 0, 0.3) solid;
        justify-content: center;
        /* height: 7vh; */
        padding-top: env(safe-area-inset-top); 
        height: calc(9vh + env(safe-area-inset-top));
    }
    .auth .header span {
        font-size: 1.2em;
    }
    .auth .header img {
        display: block;
        height: 3.8vh;
    }
    .auth .header a {
        margin-left: 4%;
    }
    .auth form {
        width: 100%;
        margin: 0 auto;
        margin-top: 2vh;
    }
    .auth .page-content{
        background: white;
    }
    .auth .page-content .illustration {
        display: none;
    }
    .auth h1 {
        font-size: 1.9em;
    font-weight: 700;
    line-height: 20px;
    }
    .auth form .input_block_section {
        margin-bottom: 50px;
    }
    .auth form .input_block_section input {
        font-size: 17px;
        width: 95%;
        padding-left: 0;
    }
    .auth form .actions {
        width: 100%;
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        margin-top: 7vh;
    }
    .auth form .actions.absolute-on-mobile {
        position: absolute;
        bottom: 4%;
        left: 0;
    }
    .auth form .actions button {
        width: 100%;
        min-width: auto;
        margin-right: 0;
        font-size: 18px;
        padding: 15px;
        border-radius: 10px;
    }
    .auth form .actions.absolute-on-mobile button{
        width: 90%;
    }
    .auth form .actions a {
        margin-bottom: 2vh;
        font-size: 16px;
    }
    .auth form a.link{
        font-size: 16px;
    }

    .auth .recaptcha{
        display: flex;
        justify-content: center;
        width: 97.5%;
    }

    .auth .confirm-info{
        line-height: 22px;
    }
}