.mobile-menu{
    position: fixed;
    width: 100%;
    /* height: calc(10vh + env(safe-area-inset-bottom)); */
    /* height: 10vh; */
    bottom: 0;
    left: 0;
    background: white;
    display: none;
    /* align-items: flex-end; */
    justify-content: space-around;
    box-sizing: border-box;
    padding: 0% 4%;
    z-index: 0;
    box-shadow: 0px -0.5px 0px rgba(0, 0, 0, 0.3);
    /* padding-top: env(safe-area-inset-bottom); */
    padding-top: 2vh;
    /* padding-bottom: calc(2vh + env(safe-area-inset-bottom, 1px)); */
    padding-bottom: max(env(safe-area-inset-bottom), 2vh);
}

.mobile-menu .menu-link{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.mobile-menu .menu-link a {
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
} 

.mobile-menu .menu-link .img{
    width: 23px;
    height: 23px;
}


.mobile-menu .menu-link .img.home{
    background: url('/assets/svg/mobile-home.svg');
    background-position: center;
     /* background-size: cover; */
     background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.tasks{
    background: url('/assets/svg/mobile-tasks.svg');
    background-position: center;
     /* background-size: cover; */
     background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.rating{
    background: url('/assets/svg/mobile-rating.svg');
    background-position: center;
    /* background-size: cover; */
    background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.chat{
    background: url('/assets/svg/mobile-chat.svg');
    background-position: center;
     /* background-size: cover; */
     background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.files{
    background: url('/assets/svg/mobile-files.svg');
    background-position: center;
    /* background-size: cover; */
    background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.list{
    background: url('/assets/svg/mobile-list.svg');
    background-position: center;
    /* background-size: cover; */
    background-repeat: no-repeat;
}

.mobile-menu .menu-link img{
    width: 23px;
    height: 23px;
}

.mobile-menu .menu-link span{
    font-weight: 600;
    font-size: 11px;
    line-height: 12px;
    text-align: center;
    letter-spacing: -0.24px;
    color: #B0B0B0;
    margin-top: 7px;
}

.mobile-menu .menu-link img:hover + span,
.mobile-menu .menu-link .img:hover + span{
    color: #007AFF;
}

.mobile-menu .menu-link .img.home:hover{
    background: url('/assets/svg/mobile-home-blue.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.tasks:hover{
    background: url('/assets/svg/mobile-tasks-blue.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.rating:hover{
    background: url('/assets/svg/mobile-rating-blue.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.chat:hover{
    background: url('/assets/svg/mobile-chat-blue.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.files:hover{
    background: url('/assets/svg/mobile-files-blue.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.mobile-menu .menu-link .img.list:hover{
    background: url('/assets/svg/mobile-list-blue.svg');
    background-position: center;
    background-repeat: no-repeat;
}


@media (max-device-width: 480px) {
    .mobile-menu{
        display: flex;
    }
}