.popup {
    position: fixed;
    /* width: 45%; */
    min-width: 25%;
    /* min-height: 40vh; */
    min-height: 295px;
    background: white;
    border-radius: 26px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    box-shadow: 0px 8px 16px 0px rgb(0 0 0 / 9%);
    display: flex;
    padding: 4% 3%;
    animation: smoothPopup 0.4s;
    /* max-height: 90vh;
    overflow-x: auto; */
    z-index: 2;
}

.popup h2 {
    font-size: 30px;
    color: #151B26;
    font-weight: bold;
    text-align: center;
    margin-bottom: 0;
}

.popup p {
    font-weight: 500;
    font-size: 13px;
    /* font-size: 20px; */
    /* line-height: 24px; */
    text-align: center;
    color: #151B26;
    margin-bottom: 60px;
}

.popup input[type="file"] {
    /* margin-bottom: 25px; */
    /* margin-top: 10%; */
    cursor: pointer;
    padding: 1rem 0rem;
    color: #5a5a5a;
    font-size: 15px;
    font-family: Montserrat;
    /* margin-bottom: 71%; */
}

.popup input[type="date"]::-webkit-calendar-picker-indicator {
    color: #A3A3A3;
}

.popup button {
    /* width: 70%; */
    /* margin: 0 auto; */
}

.popup .form_content {
    display: flex;
    flex-direction: column;
    align-items: inherit;
}

.popup .close {
    position: absolute;
    width: 14px;
    /* right: 5%;
    top: 5%; */
    top: 20px;
    right: 20px;
    cursor: pointer;
}

.popup .circle {
    width: 178px;
    height: 178px;
    border-radius: 50%;
    background: #D8D8D8;
}

.popup .side {
    width: 270px;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    margin: 0 4%;
    /* width: 23vw; */
    width: 26vw;
}

.popup .error_block {
    width: 100%;
    /* height: 200px; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 33vw;
}

.popup .error_block .cancel {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #A3A3A3;
    cursor: pointer;
    margin-bottom: -5vh;
}

.popup .error_text {
    font-size: 16px;
    font-weight: bold;
    color: #151B26;
    margin-bottom: 4vh;
    margin-top: 4vh;
}

.popup .error_block button.main {
    background: linear-gradient(45deg, #982c2c 0%, #da51ad 100%), #ff0000;
}

.popup .popup_form {
    width: 36vw;
    position: relative;
    /* max-height: 80vh;
    overflow-x: auto; */
}

.popup .popup_form h4 {
    margin-top: 0;
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 33px;
}

.popup .form_elements {
    max-height: 65vh;
    overflow-x: auto;
    /* padding-bottom: 5vh; */
    box-sizing: border-box;
}

.popup .form_elements.upload{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    width: 400px;
}

.popup .form_elements.upload .popup-illustration{
    margin: 3vh 0;
}

.popup .form_action.upload{
    margin-top: 0.5vh;
    margin-bottom: 1vh;
}

.popup .info_block {
    display: flex;
    flex-direction: column;
    margin-top: 4vh;
    position: relative;
}

.popup .info_block .label {
    color: #686868;
    font-weight: 400;
    font-size: 13px;
    margin-bottom: 1vh;
}

.popup .info_block .info {
    color: #151B26;
    font-weight: 600;
    font-size: 17px;
}

.popup .delete-action {
    color: #F00808;
    font-weight: 600;
    font-size: 17px;
    margin-top: 4vh;
}

.popup .form_input_section {
    display: flex;
    flex-direction: column-reverse;
}

.popup .form_input_section .label {
    display: block;
    font-weight: 500;
    font-size: 12px;
    line-height: 17px;
    color: #717171;
    margin-bottom: 8px;
    transition: 0.9s;
}

.popup .checkbox-info {
    color: #717171;
}

.popup .form_input_section input {
    border: 1px solid #D6D6D6;
    box-sizing: border-box;
    border-radius: 10px;
    font-weight: 500;
    font-size: 15px;
    line-height: 26px;
    color: #686868;
    display: block;
    width: 100%;
    /* padding: 5% 8%; */
    padding: 13px 18px;
    margin-bottom: 3vh;
    transition: 0.1s;
    font-family: Montserrat;
}

.popup .form_input_section input::placeholder, .popup .form_input_section textarea::placeholder {
    color: #B0B0B0;
}

.popup .form_input_section input:focus, .popup .form_input_section textarea:focus {
    border: 2px solid #1A73E8;
    color: #1A73E8;
}

.popup .form_input_section input:focus ~ .label, .popup .form_input_section textarea:focus ~ .label {
    color: #1A73E8;
}


.popup .form_input_section textarea {
    border: 1px solid #D6D6D6;
    box-sizing: border-box;
    border-radius: 10px;
    font-weight: 600;
    font-size: 15px;
    line-height: 26px;
    color: #686868;
    display: block;
    width: 100%;
    /* padding: 5% 8%; */
    padding: 13px 18px;
    margin-bottom: 3vh;
    transition: 0.1s;
    font-family: Montserrat;
    height: 20vh;
    resize: none;
}

.popup .form_input_section select {
    border: 1px solid #D6D6D6;
    box-sizing: border-box;
    border-radius: 10px;
    font-weight: 500;
    font-size: 15px;
    line-height: 26px;
    color: #686868;
    display: block;
    width: 100%;
    /* padding: 5% 8%; */
    padding: 13px 18px;
    margin-bottom: 3vh;
    transition: 0.1s;
    font-family: Montserrat;
}

.popup .form_input_section input[type="time"] {
    border: 1px solid #D6D6D6;
    box-sizing: border-box;
    border-radius: 10px;
    font-weight: 500;
    font-size: 15px;
    line-height: 26px;
    color: #686868;
    display: block;
    width: 100%;
    /* padding: 5% 8%; */
    padding: 13px 18px;
    margin-bottom: 3vh;
    transition: 0.1s;
    font-family: Montserrat;
}

.popup .form_radio_color_section input[type="radio"] {
    visibility: hidden;
    height: 0;
    width: 0;
}

.popup .form_radio_color_section input[type="radio"]+label {
    cursor: pointer;
    width: 16px;
    height: 16px;
    display: inline-block;
    border-radius: 50px;
    /* background: lightgray; */
    transition: all 0.3s ease-out;
}

.popup .form_radio_color_section span {
    display: block;
    font-weight: 500;
    font-size: 12px;
    line-height: 17px;
    color: #717171;
    margin-bottom: 16px;
    transition: 0.9s;
}

.popup .form_radio_color_section .color_circle {
    cursor: pointer;
    width: 16px;
    height: 16px;
    display: inline-block;
    border-radius: 50px;
    /* background: lightgray; */
    transition: all 0.3s ease-out;
    margin-right: 8px;
}

.popup .form_radio_color_section .color_circle:hover {
    width: 28px;
}

.popup .form_radio_color_section input[type="radio"]:checked+label {
    width: 28px;
}

