    background: #3170E9;
}

.popup .form_radio_color_section .green {
    background: #12BE4D;
}

.popup .form_radio_color_section .red {
    background: #F00808;
}

.popup .form_radio_color_section .pink {
    background: #BA49FF;
}

.popup .form_radio_color_section .yellow {
    background: #FFBB0E;
} */

.popup .form_action {
    /* min-width: 43%; */
    float: right;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.33em;
    margin-bottom: -3vh;
}

.popup .form_action.small-margin {
    margin-top: 2.5vh;
    margin-bottom: 3vh;
}

.popup .form_action .cancel {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #A3A3A3;
    margin-right: 30px;
    cursor: pointer;
}

.popup .bottom-gradient {
    /* width: 100.1%;
    height: 140px;
    position: absolute;
    bottom: 5vh;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 63.85%, rgba(255, 255, 255, 0.95) 100%); */

    /* width: 100.1%;
    height: 20px;
    position: absolute;
    bottom: 5vh;
    background: linear-gradient(205.04deg, rgba(255, 255, 255, 0) 15.9%, #FFFFFF 84.08%); */

    width: 100%;
    height: 5vh;
    position: sticky;
    /* bottom: -5vh; */
    bottom: -1px;
    margin-top: -1px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 15.9%, white 84.08%);
    left: 0;
}

.popup-overlay {
    /* display: none; */
    background: #0000007d; 
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 1;
    left: 0;
    /* top: 0; */
    bottom: 0;
}

.popup .confirm {
    flex-direction: column;
    padding: 8%;
    position: relative;
    max-width: 25vw;
    text-align: center;
}

.popup .confirm .info {
    font-weight: 600;
    color: #151B26;
    font-size: 22px;
}

.popup .confirm-action {
    font-weight: 600;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    width: 50%;
    position: absolute;
    bottom: 0%;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
}

.popup .confirm-action .yes {
    color: #F00808;
    cursor: pointer;
}

.popup .confirm-action .no {
    color: #9D9D9D;
    cursor: pointer;
}

.role-menu {
    width: 150px;
    height: 80px;
    position: absolute;
    right: 0;
    top: 0;
    background: #2F2F2F;
    /* border: 0.5px solid #CECECE; */
    border-radius: 5px;
    color: #FFFFFF;
    box-sizing: border-box;
    /* padding: 8%; */
    font-weight: 500;
    display: none;
}

.role-menu .line {
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: inherit;
    cursor: pointer;
    transition: 0.5s;
}

.role-menu .line:hover {
    background: #1d1c1c;
}

.role-menu .line.blue {
    background: #3170E9;
    border-radius: 5px 5px 0px 0px;
}

.role-menu .line img {
    left: 20px;
    position: absolute;
    width: 12px;
}

.popup .mobile-block {
    display: none;
    width: 90%;
    margin: 0 auto;
}

.popup .popup-illustration{
    width: 100%;
    height: 29vh;
}

/* Custom */

.popup .create-join-class-popup .form_action .cancel {
    margin-top: 1vh;
}

.popup .create-join-class-popup .popup_form h4{
    margin-bottom: 3vh;
    text-align: center;
    font-size: 23px;
    line-height: 26px;
    margin-top: 4.3vh;
}

.popup .create-join-class-popup .form_action button.space{
    margin-bottom: 2.5vh;
}

@media (min-width: 1000px) and (max-width: 1024px) {}

@media (min-width: 768px) and (max-width: 1000px) {
    .popup {}
    .popup .popup_form {
        width: 46vh;
    }
    .popup .form_action {
        margin-bottom: -2vh;
    }
    .popup .side {
        width: 27vh;
    }
}

@media (max-device-width: 480px) {
    .popup .pc-only {
        display: none;
    }
    .popup .mobile-block {
        display: block;
    }
    .popup {
        width: 101%;
        /* max-width: 100%;
        min-width: 85%; */
        border-radius: 16px;
    }
    .popup.small {
        width: 80%;
        min-height: 22vh;
        padding: 0;
    }
    .popup .popup_form {
        width: 90%;
        margin: 0 auto;
    }
    .popup .form_elements {
        max-height: 62vh;
        /* overflow-x: auto; */
        overflow-x: scroll;
    }
    .popup .form_action {
        margin-bottom: 2vh;
        width: 100%;
        flex-direction: column-reverse;
        margin-top: 1vh;
        justify-content: center;
    }
    .popup .form_action button {
        width: 100%;
        font-size: 16px;
        padding: 12px 25px;
    }
    .popup .form_action .cancel {
        margin-right: 0;
        margin-top: 2vh;
    }
    .popup .bottom-gradient {
        bottom: calc(-5vh + -1px);
    }
    .popup .popup_form h4 {
        margin-bottom: 4vh;
        margin-top: 3vh;
    }
    .popup .close {
        /* right: 3%;
        top: 3%; */
        display: none;
    }
    .popup-overlay {
        display: block;
        background: #000000b8;
        overflow: hidden;
        height: 100vh;
    }
    .popup .confirm {
        /* max-width: 80%; */
        padding-top: 15%;
        margin: 0 auto;
        max-width: 100%;
    }
    .popup .confirm-action {
        bottom: 0;
        border-top: 1px solid #F2F2F2;
        width: 100%;
        height: 33%;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    .popup .confirm-action .yes, .popup .confirm-action .no {
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .popup .confirm-action .yes {
        border-right: 1px solid #F2F2F2;
    }
    .popup .confirm .info {
        font-size: 18px;
    }
    .popup .error_block{
        width: 100%;
        min-height: 47vh;
    }
    .popup .form_content.upload{
        width: 100%;
        box-sizing: border-box;
        display: grid;
        grid-template-columns: 100%;
    }
    .popup .form_elements.upload{
        width: 100%;
        align-items: flex-start;
        /* margin-bottom: -4vh; */
    }
    .popup .form_elements.upload + button{
        width: 100%;
    }
    .popup input[type="file"]{
        margin: 0 auto;
        /* margin-bottom: 4vh; */
        width: 70%;
    }

    .popup .checkbox-line{
        display: flex;
    }
    .popup .checkbox-line span{
        margin-left: 6px;
    }

    .popup .form_elements.upload .popup-illustration{
        width: 70%;
        margin: 0 auto;
        margin-top: 3vh;
        margin-bottom: 1vh;
    }


    .popup .form_radio_color_section input[type="radio"]+label{
        width: 19px;
        height: 19px;
    }
}