.taskpage {}

.taskpage .wrapper {
    display: flex;
}

.taskpage .side-l {
    width: 49%;
    box-sizing: border-box;
    padding-right: 4%;
    border-right: 1px solid #D6D6D6;
}

.taskpage .side-r {
    width: 49%;
    box-sizing: border-box;
    padding-left: 4%;
}

.taskpage .task_status {
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 7vh;
}

.taskpage .task_status.red {
    color: #F00808;
}

.taskpage .task_status.blue {
    color: #3170E9;
}

.taskpage .task_status.green {
    color: #12BE4D;
}

.taskpage .page_heading{
    align-items: baseline;
}

.taskpage .task_heading {
    color: #151B26;
    font-weight: 500;
    font-size: 27px;
    display: flex;
    /* align-items: center; */
    align-items: baseline;
    margin-bottom: 2vh;
}

.taskpage .edit_task_icon {
    width: 26px;
    margin-left: 20px;
    cursor: pointer;
}

.taskpage .task_circle {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    display: block;
    margin-right: 11px;
    display: table;
}

.taskpage .task_deadline {
    color: #3170E9;
    font-size: 16px;
    margin-bottom: 4vh;
    font-weight: 500;
}

.taskpage .task_description {
    font-size: 15px;
    line-height: 21px;
    color: #475773;
    margin-bottom: 8vh;
    word-break: break-word;
}

.taskpage .homework-info-line{
    width: 100%;
    display: flex;
    /* align-items: center; */
    justify-content: space-between;
}

.taskpage .homework-info-line .blue{
    color: #3170E9;
    font-size: 16px;
    margin-bottom: 4vh;
    font-weight: 500;
}

.taskpage .homework-info-line .gray{
    font-weight: 500;
    font-size: 16px;
    color: #475773;
}

.taskpage textarea.work {
    width: 100%;
    /* color: #A3A3A3; */
    background: #FFFFFF;
    border: 1px solid #E5E5E5;
    box-sizing: border-box;
    border-radius: 20px;
    font-size: 14px;
    padding: 3%;
    height: 25vh;
    letter-spacing: 0.04em;
    resize: none;
}

.taskpage textarea.work::placeholder {
    color: #A3A3A3;
}

.taskpage button.send-work {
    float: right;
}

.taskpage .step{
    margin-bottom: 2vh;
    font-size: 15px;
    color: #475773;
}

.taskpage .step.margin{
    margin-top: 5vh;
}

.taskpage .homework-post {
    display: flex;
    margin-bottom: 0.5vh;
}

.taskpage .homework-post .user-text {
    display: inline-block;
    margin-left: 12px;
}

.taskpage .homework-post img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.taskpage .homework-post span {
    display: block;
    font-size: 12px;
    color: #475773;
    font-weight: 600;
}

.taskpage .homework-post p {
    color: #475773;
    font-size: 15px;
    margin-top: 1px;
    max-width: 100%;
    font-weight: 400;
    line-height: 21px;
}

.taskpage .homework-link {
    background: white;
    margin-bottom: 2vh;
    border: solid 1px #E8E8E8;
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5% 3%;
    font-size: 15px;
}

.taskpage .homework-link .user-part {
    display: flex;
    align-items: center;
}

.taskpage .homework-link .user-part img {
    width: 47px;
    height: 47px;
    border-radius: 50%;
    object-fit: cover;
}

.taskpage .homework-link .user-part span {
    margin-left: 28px;
    color: #151B26;
    font-weight: 500;
    font-size: 16px;
}

.taskpage .homework-link .info-action-part span {
    margin-right: 78px;
    color: #3170E9;
    font-weight: 500;
}

.taskpage .homework-link .info-action-part span.green {
    color: #12BE4D;
}

.taskpage .blue-heading {
    margin-top: 12vh;
    font-weight: 500;
    color: #3170E9;
    font-size: 34px;
    margin-bottom: 4vh;
}

.taskpage .homework-link .action {
    color: #3170E9;
    text-decoration-line: underline;
    font-weight: 400;
}

.taskpage .link-wrapper {
    text-decoration: none;
}

.taskpage .files-block {}

.taskpage .files-block.user{
    margin-top: 4vh;
}

.taskpage .files-block .files-action {
    color: #1A73E8;
    font-weight: 500;
    font-size: 15px;
    text-decoration-line: underline;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.taskpage .files-block .files-action img{
    margin-right: 8px;
    width: 21px;
    height: 21px;
} 


.taskpage .files-message{
    color: #475773;
}

.taskpage .files-message-admin{
    color: #475773;
    margin-top: 5vh;
}

.taskpage .files-list{
    font-weight: 500;
    font-size: 17px;
    margin-top: 5vh;
}

.taskpage .files-list .file-line{
    margin-top: 2vh;
    border-bottom: 1px solid #D6D6D6;
    padding-bottom: 2vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.taskpage .files-list .file-line span.name{
    color: #252F42;
}

.taskpage .files-list .file-line .right-side{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.taskpage .files-list .file-line span.type{
    color: #3170E9;
    font-size: 14px;
    text-align: right;
    font-weight: 400;
    margin-right: 16px;
}

.taskpage .files-list .file-line a{
    display: flex;
}

.taskpage .files-list .file-line img{
    width: 30px;
    height: 30px;
    cursor: pointer;
}

.taskpage .files-list .file-line img.delete{
    width: 12px;
    height: 12px;
    margin-left: 18px;
}

@media (max-device-width: 480px) {
    .taskpage{
        padding-bottom: calc(10vh + env(safe-area-inset-bottom)); 
    }
    .taskpage .wrapper {
        flex-direction: column;
    }
    .taskpage .side-r, .taskpage .side-l {
        width: 100%;
        padding-right: 0;
        padding-left: 0;
        border-right: 0;
    }
    .taskpage .side-l {
        border-bottom: 1px solid #D6D6D6;
        margin-bottom: 28px;
    }
    .taskpage .task_heading, .app h4 {
        font-size: 20px;
        /* margin-top: 1vh; */
    }
    .taskpage .student-files-heading{
        margin-top: 28px;
    }
    .taskpage .task_circle {
        width: 10px;
        height: 10px;
    }
    .taskpage .task_description {
        margin-bottom: 6vh;
        font-weight: 300;
        line-height: 27px;
    }
    .taskpage .blue-heading {
        font-size: 26px;
        margin-top: 12vh;
    }
    .taskpage .homework-link .action {
        display: none;
    }
    .taskpage .homework-link .info-action-part span {
        margin-right: 0;
        font-size: 12px;
    }
    .taskpage .homework-link .user-part span {
        font-size: 12px;
    }
    .taskpage .homework-link .user-part img {
        width: 42px;
        height: 42px;
    }
    .taskpage .task_deadline {
        margin-bottom: 2vh;
    }
    .taskpage .task_deadline.second{
        margin-bottom: 3vh;
    }
    .taskpage .homework-post{
        margin-bottom: 1vh;
    }
    .taskpage button.send-work{
        float: unset;
        width: 100%;
    }
}