.userpage {
    box-sizing: border-box;
    /* padding: 1% 4%; */
    display: flex;
}

.userpage .left-side {
    width: 50%;
    border-right: 1px solid #D6D6D6;
    box-sizing: border-box;
}

.userpage .right-side {
    width: 50%;
    box-sizing: border-box;
    padding: 0% 5%;
    padding-top: 78px;
}

.userpage .right-side h3 {
    /* line-height: 78px; */
    margin-top: 4vh;
    margin-bottom: 28px;
}

.profile-settings{
    margin-top: 4vh;
}

.profile_picture {
    /* margin-top: 50px; */
    margin-bottom: 40px;
}

.profile_picture span, .profile_settings_input_block span {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #151B26;
    display: block;
    margin-bottom: 16px;
}

.profile_picture_line {
    display: flex;
    align-items: center;
    /* justify-content: space-between;
    width: 21%; */
}

.profile_picture_line img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
}

.profile_picture_line span.upload {
    font-size: 18px;
    line-height: 22px;
    color: #3170E9;
    cursor: pointer;
    margin-bottom: 0;
    margin-left: 18px;
}

.profile_settings_input_block {
    margin-bottom: 40px;
}

.profile_settings_input_block input {
    font-weight: 500;
    font-size: 21px;
    line-height: 28px;
    color: #686868;
    background: none;
    border: none;
    border-bottom: 1px solid #A3A3A3;
    padding-bottom: 14px;
    /* width: 45%; */
    width: 90%;
    font-family: Montserrat;
}

.profile_settings_input_block input::placeholder {
    color: #B0B0B0;
}

.profile_settings_input_block input:focus {
    border-bottom: 1px solid #3170E9;
}

.profile_edit {
    width: 24px;
    position: absolute;
    margin-left: -24px;
}

.logout {
    font-weight: 400;
    font-size: 20px;
    line-height: 35px;
    color: #F00808;
    cursor: pointer;
}

.logout.pc {

}

.logout.mobile {
    display: none;
}

.userpage .plan {
    width: 100%;
}

.userpage .plan .plan-header {
    background: linear-gradient(45deg, #4937E8 0%, #2A82E9 100%), #C4C4C4;
    box-sizing: border-box;
    padding: 5%;
    border-radius: 16px 16px 0 0;
}

.userpage .plan .top-line{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.userpage .plan h4 {
    line-height: 4.5vh;
    color: #F9F9F9;
    font-weight: 600;
    font-size: 25px;
    margin-bottom: 1vh;
    margin-top: 0;
}

.userpage .plan .plan-header p {
    color: #fff;
    font-size: 13px;
    display: flex;
}

.userpage .plan .plan-header p img{
    margin-left: 8px;
    cursor: pointer;
}

.userpage .plan .plan-header span {
    color: #FFFFFF;
    font-size: 13px;
}

.userpage .plan .plan-info {
    box-sizing: border-box;
    padding: 2% 5%;
    background: white;
    border: 1px solid #EEEEEE;
    border-radius: 0 0 16px 16px;
    display: flex;
    justify-content: space-between;
}

.userpage .plan .plan-info p {
    color: #151B26;
    font-size: 13px;
    line-height: 20px;
}

.userpage .plan .plan-info ul {
    width: 70%;
    color: #151B26;
    font-size: 14px;
}


.userpage .plan .plan-info ul li {
    list-style-type: none;
    margin-bottom: 2vh;
}


.userpage .plan .plan-info ul li:last-child{
    margin-bottom: 0;
}

.userpage .plan .plan-info ul li img{
    margin-right: 9px;
}


/* @media (min-width: 1000px) and (max-width: 1024px) { */
@media (min-width: 1000px) and (max-width: 1024px) {
    .profile_picture_line span.upload {
        margin-left: 18px;
        font-size: 15px;
    }
    .profile_settings_input_block input {
        font-size: 22px;
    }
    .profile_picture span, .profile_settings_input_block span {
        font-size: 15px;
    }
    .userpage .plan .plan-info p{
        width: 30%;
        line-height: 22px;
    }
    .userpage .plan .plan-info ul{
        width: 70%;
    }
}

@media (min-width: 768px) and (max-width: 1000px) {
    .profile_picture_line span.upload {
        margin-left: 20px;
        font-size: 16px;
    }
    .profile_picture span, .profile_settings_input_block span {
        font-size: 14px;
    }
    .profile_settings_input_block input {
        font-size: 20px;
    }
    .userpage .plan .plan-info{
        flex-direction: column;
        align-items: flex-end;
    }
    .userpage .plan .plan-info ul{
        width: 100%;
    }
    .userpage .plan h4{
        line-height: 3vh;
        margin-top: 1vh;
    }
    .userpage .plan .plan-header span{
        margin-top: 1vh;
    }
    .userpage .plan .plan-info ul li{
        margin-bottom: 1.2vh;
    }
}

@media (max-device-width: 480px) {
    .userpage{
        flex-direction: column;
    }
    .userpage .left-side{
        border: 0;
        width: 100%;
    }
    .userpage .right-side{
        width: 100%;
        padding: 0;
    }
    .profile_settings_input_block input {
        /* width: 100%; */
    }
    .userpage .plan .plan-info{
        flex-direction: column;
        align-items: flex-end;
        padding-bottom: 6%;
    }
    .userpage .plan .plan-info ul{
        width: 100%;
    }

    .logout.pc {
        display: none;
    }
    
    .logout.mobile {
        display: block;
        padding-bottom: env(safe-area-inset-bottom);

        font-weight: 600;
        font-size: 30px;
    }

    .userpage .right-side h3 {
        margin-top: 0vh;
    }
}