.top-menu-absolute{
    position: absolute;
    top: 0;
    right: 9%;
    height: 9vh;
    display: flex;
    align-items: center;
    animation: smoothContent 0.5s;
}

.top-menu{
    /* padding-right: 44px; */
}

.top-menu a{
    margin-left: 40px;
    text-decoration: none;
}

.top-menu span{
    color: #151B26;
    font-size: 14px;
    font-weight: 400;
    transition: 0.5s;
}

.top-menu span:hover{
    color: #3170e9;
}

@media (min-width: 1000px) and (max-width: 1024px) {
    .top-menu-absolute{
        right: 12%;
    }
}

@media (min-width: 768px) and (max-width: 1000px) {
    .top-menu-absolute{
        height: 7vh;
        right: 15%;
    }
}

@media (max-device-width: 480px) {
    .top-menu-absolute{
        display: none;
    }
}