.alltasks-page {}

.alltasks-page .undone {
    color: #3170E9;
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 7vh;
}

.alltasks-page .tasks-column-names {
    display: grid;
    width: 100%;
    margin-bottom: 3vh;
    padding: 0 4%;
    box-sizing: border-box;
    margin-top: 2vh;
}

.alltasks-page .tasks-column-names span {
    display: block;
    color: #475773;
    font-weight: 600;
    font-size: 13px;
}

.alltasks-page .tasks-column-rows {
    width: 100%
}

.alltasks-page .tasks-column-rows a {
    text-decoration: none;
}

.alltasks-page .tasks-row {
    margin-bottom: 6px;
    display: grid;
    /* grid-template-columns: 30% 25% 25% 20%; */
    background: white;
    box-sizing: border-box;
    padding: 2% 4%;
    /* padding: 1.5% 4%; */
    border-radius: 10px;
    border: 1px solid #EEEEEE;
    /* border-left: 8px solid gray; */
    position: relative;
    transition: 0.4s;
}

.alltasks-page .tasks-row:hover{
    background: #FBFBFB;
}

.alltasks-page .tasks-row, .alltasks-page .tasks-column-names {
    /* grid-template-columns: 25% 25% 14% 16% 10% 10%; */
    /* grid-template-columns: 30% 25% 14% 16% 15% 7%; */
}

.alltasks-page .tasks-row .td {
    width: 100%;
    font-size: 15px;
    font-weight: 400;
    word-break: break-word;
}

.alltasks-page .tasks-row .table_space {}

.alltasks-page .tasks-row .td.number{
    color: #3170E9;
}

.alltasks-page .tasks-row .td.user-line {
    display: flex;
    align-items: center;
}

.alltasks-page .tasks-row .td.user-line .teacher-icon{
    margin-left: 10px;
}

.alltasks-page .tasks-row .td .down-icon{
    margin-left: 9px;
    width: 11px;
    cursor: pointer;
}

.alltasks-page .tasks-row .td .member-role{
    cursor: pointer;
}

.alltasks-page .tasks-row .td.rating{
    color: #3170E9;
}

.alltasks-page .tasks-row .td.role{
    text-transform: capitalize;
}

.alltasks-page .tasks-row .td.more{
    display: none;
}

.alltasks-page .tasks-row .td.more img{
    width: 6px;
}

.alltasks-page .tasks-row .td.user-picture img{
    object-fit: cover;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.alltasks-page .tasks-row .td.delete img {
    cursor: pointer;
}

.alltasks-page .tasks-row .name {
    color: #151B26;
    font-weight: 600;
}

.alltasks-page .tasks-row .topic {
    color: #151B26;
}

.alltasks-page .tasks-row .deadline {
    color: #3170E9;
}

.alltasks-page .tasks-row .published, .alltasks-page .tasks-row .mark {
    color: #151B26;
}

.alltasks-page .tasks-row .status.red {
    color: #F00808;
}

.alltasks-page .tasks-row .status.blue, .alltasks-page .tasks-row .status-m.blue {
    color: #3170E9;
}

.alltasks-page .tasks-row .status.green, .alltasks-page .tasks-row .status-m.green {
    color: #12BE4D;
    display: inline-block;
}

.alltasks-page .short_description_mobile {
    display: none;
}

/* Tasks Border Colors */

.alltasks-page .tasks-row.blue {
    border-left: 8px solid var(--task-color-blue);
}

.alltasks-page .tasks-row.green {
    border-left: 8px solid var(--task-color-green);
}

.alltasks-page .tasks-row.red {
    border-left: 8px solid var(--task-color-red);
}

.alltasks-page .tasks-row.pink {
    border-left: 8px solid var(--task-color-pink);
}

.alltasks-page .tasks-row.yellow {
    border-left: 8px solid var(--task-color-yellow);
}

.alltasks-page .tasks-row .published-m {
    display: none;
}
.alltasks-page .tasks-row .status-m {
    display: none!important;
}

.alltasks-page .delete-action{

}

.alltasks-page .delete-action .delete_icon{
    width: 21px;
    height: 21px;
    cursor: pointer;
}

