.alltasks-page .delete-action-mobile{
    display: none;
} */


@media (max-device-width: 480px) {
    .admin-lessons .members-row{
        padding: 7%;
    }
    .alltasks-page .tasks-column-names {
        display: none;
    }
    .alltasks-page .tasks-row .topic, .alltasks-page .tasks-row .deadline, .alltasks-page .tasks-row .status, .alltasks-page .tasks-row .published, .alltasks-page .tasks-row .mark {
        display: none!important;
    }
    .alltasks-page .short_description_mobile {
        display: block;
        font-size: 13px;
        line-height: 16px;
        color: #475773;
        margin-top: 2vh;
    }
    .alltasks-page .tasks-row .published-m {
        color: #3170E9;
        display: block;
    }
    .alltasks-page .tasks-row .published-m, .alltasks-page .tasks-row .status-m {
        font-size: 15px;
        display: block!important;
        /* float: right; */
    }
    .alltasks-page .tasks-row .name {
        font-size: 19px;
        font-weight: 600;
        color: #151B26;
        display: inline-block;
        width: 70%!important;
        margin-bottom: 3vh;
    }
    .alltasks-page .tasks-row {
        display: block;
        padding: 7%;
        margin-bottom: 8px;
    }
    .alltasks-page .tasks-row .td {
        width: auto;
    }
    .alltasks-page .tasks-row .table_space{
        display: none!important;
    }
    .alltasks-page .tasks-row .status.blue, .alltasks-page .tasks-row .status-m.blue{
        display: block;
    }


    .alltasks-page .tasks-row.members{
        display: grid;
        grid-template-columns: 8% 20% 70% 15%;
        padding: 4% 5%;
    }

    .alltasks-page .tasks-row .td.pc-only{
        display: none;
    }

    .alltasks-page .tasks-row .td.more{
        display: flex;
    }

    /* .alltasks-page .delete-action-pc{
        display: none!important;
    } */

    /* .alltasks-page .delete-action-mobile{
        display: inline-block;
        float: right;
    } */

    .alltasks-page .delete-action{
        position: absolute;
        right: 7%;
        top: 0;
        padding-top: 7%;
    }
    
}