.group_settings_page {}

.group_settings_page .main_form {
    margin-top: 2%;
}

.group_settings_page .logo-circle{
    background: white;
    color: #3761e8;
    width: 60px;
    height: 60px;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    border-radius: 50%;
    box-sizing: border-box;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    /* cursor: pointer; */
    /* margin-right: 15px; */
}

/* Homework Admin Page */

.header-user-line {
    display: flex;
    align-items: center;
    margin-bottom: 6vh;
}

.header-user-line img {
    width: 38px;
    height: 38px;
    border-radius: 50%;
    object-fit: cover;
}

.header-user-line span {
    margin-left: 12px;
    font-weight: 600;
    color: #151B26;
    font-size: 16px;
}

.homework .teacher-comment {
    position: relative;
    margin-bottom: 4vh;
}

.homework .teacher-comment textarea {
    width: 100%;
    /* color: #A3A3A3; */
    background: #FFFFFF;
    border: 1px solid #E5E5E5;
    box-sizing: border-box;
    border-radius: 20px;
    font-size: 14px;
    padding: 3%;
    height: 15vh;
    letter-spacing: 0.04em;
    resize: none;
}

.homework .teacher-comment-button {
    width: 35px;
    height: 35px;
    position: absolute;
    bottom: 15px;
    right: 12px;
    cursor: pointer;
}

.homework .stroke {
    background: #E5E5E5;
    display: block;
    height: 1px;
    width: 100%;
    margin-top: 2vh;
    margin-bottom: 4vh;
}

.admin-lessons .delete_icon {
    width: 21px;
    height: 21px;
    cursor: pointer;
}

.admin-lessons .lessons-row {
    grid-template-columns: 25% 25% 25% 25% 1%;
}

.admin-lessons .lessons-row .blue-text {
    color: #3170E9;
}

.admin-lessons .lessons-row .blue-text-mobile {
    color: #3170E9;
    display: none;
}

.admin-lessons .lessons-row .delete-action-mobile{
    display: none;
}

.admin-lessons .lessons-row .recurring-status.mobile {
    display: none;
}


.admin-lessons .members-row {
    grid-template-columns: 10% 6% 36% 29% 17% 4%;
    padding: 1.2% 4%;
}

.admin-lessons .members-row .td{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: #151B26;
    position: relative;
}

.admin-lessons .members-row .role-menu{
    width: 150px;
    height: 80px;
    position: absolute;
    left: 0;
    top: 0;
    background: #2F2F2F;
    /* border: 0.5px solid #CECECE; */
    border-radius: 5px;
    color: #FFFFFF;
    box-sizing: border-box;
    /* padding: 8%; */
    font-weight: 500;
    display: none;
    z-index: 2;
}

.admin-lessons .members-row .role-menu .line{
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: inherit;
    cursor: pointer;
    transition: 0.5s;
}

.admin-lessons .members-row .role-menu .line:hover{
    background: #1d1c1c;
}

.admin-lessons .members-row .role-menu .line.blue{
    background: #3170E9;
    border-radius: 5px 5px 0px 0px;
}

.admin-lessons .members-row .role-menu .line img{
    left: 20px;
    position: absolute;
    width: 12px;
}

.leave{
        font-weight: 400;
        font-size: 20px;
        line-height: 35px;
        color: #F00808;
        cursor: pointer;
}

@media (max-device-width: 480px) {
    .homework .stroke {
        margin-bottom: 0;
        display: none;
    }
    .admin-lessons .lessons-row .blue-text {
        display: none;
    }
    .admin-lessons .lessons-row .blue-text-mobile {
        display: inline-block;
    }
    .admin-lessons .lessons-row .pc-line {
        display: none;
    }
    .admin-lessons .lessons-row .recurring-status {
        color: #475773;
        display: inline-block;
        float: right;
    }
    .admin-lessons .lessons-row .recurring-status.pc {
        display: none;
    }
    .admin-lessons .lessons-row .recurring-status.mobile {
        display: block;
    }
    .admin-lessons .lessons-row .delete-action-mobile{
        display: inline-block;
        float: right;
    }
    .admin-lessons .lessons-row .delete-action-pc{
        display: none;
    }
    .admin-lessons .lessons-row .name{
        margin-bottom: 3vh;
    }
    .admin-lessons .lessons-row .short_description_mobile{
        font-size: 13px;
        font-weight: 400;
    }

    .group_settings_page form.mid-size{
        width: 100%;
    }
}