* {
  outline: none;
  -webkit-locale: auto;
  white-space: normal;
}

::-webkit-scrollbar {
  width: 0px;
  /* Remove scrollbar space */
  background: transparent;
  /* Optional: just make scrollbar invisible */
}

:root {
  --task-color-blue: #3170E9;
  --task-color-green: #12BE4D;
  --task-color-pink: #BA49FF;
  --task-color-red: #F00808;
  --task-color-yellow: #FFBB0E;
}

body {
  margin: 0 auto;
  background: #F9F9F9;
  /* padding-top: env(safe-area-inset-top); */
}

.app {
  font-family: Montserrat;
  font-style: normal;
  /* animation: smoothContent 0.3s; */
}

.app textarea, .app input {
  font-family: Montserrat;
  font-style: normal;
}

.app .page {
  width: 95%;
  float: right;
}

.app .page_content {
  box-sizing: border-box;
  padding: 3% 3%;
  animation: smoothContent 0.4s;
  /* animation: smoothContent 0.1s; */
}

.app .page_content .action-space {
  padding-top: 1%;
}

.app .page_heading {
  font-weight: 700;
  font-size: 45px;
  line-height: 78px;
  color: #151B26;
  display: flex;
  align-items: center;
  max-width: 65%;
}

.app .page_heading.space {
  margin-bottom: 4vh;
}

.app .page_heading .actions_block {
  margin-left: 20px;
  position: relative;
}

.app a.icon-link {
  display: flex;
}

.app .home {}

.app .home .illustration-area {
  position: relative;
  width: 20%;
  margin: 0 auto;
}

.app .home .magic-plus {
  width: 30%;
  height: 11vh;
  max-height: 150px;
  /* border: 1px solid gray; */
  cursor: pointer;
  position: absolute;
}

.app .home .magic-plus.mobile {
  display: none;
}

.app .home .illustration {
  display: block;
  margin: 0 auto;
  margin-top: 3%;
  /* width: 20%; */
  width: 100%;
}

.app .home p {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 30px;
  text-align: center;
  /* color: #8C8C8C; */
  color: #151B26;
  margin-top: 4vh;
}

.app h1 {
  font-weight: bold;
  font-size: 64px;
  line-height: 78px;
  color: #151B26;
}

.app h3 {
  font-weight: 600;
  font-size: 30px;
  line-height: 35px;
  color: #151B26;
  margin-bottom: 28px;
}

.app h4 {
  font-weight: 500;
  font-size: 27px;
  line-height: 35px;
  color: #151B26;
  margin-top: 0;
  margin-bottom: 28px;
}

button.main {
  background: linear-gradient(45deg, #4937E8 0%, #2A82E9 100%), #C4C4C4;
  border-radius: 11px;
  border: 0;
  padding: 10px 25px;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 24px;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;
  min-width: 130px;
  transition: background 1s ease-out;
  font-family: Montserrat;
}

button.main:hover {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)), linear-gradient(45deg, #4937E8 0%, #2A82E9 100%);
}

button.main.gray {
  background: #A5A5A5;
  pointer-events: none;
}

form .input_block_section {
  margin-bottom: 40px;
}

form .input_block_section span {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #151B26;
  display: block;
  margin-bottom: 13px;
}

form .input_block_section span.info {
  font-size: 12px;
  color: #686868;
  margin-top: -11px;
}

form .input_block_section input {
  font-weight: 500;
  font-size: 21px;
  line-height: 28px;
  color: #686868;
  background: none;
  border: none;
  border-bottom: 1px solid #A3A3A3;
  padding-bottom: 14px;
  font-family: Montserrat;
  width: 100%;
}

form .input_block_section input::placeholder {
  color: #B0B0B0;
}

form .input_block_section input:focus {
  border-bottom: 1px solid #3170E9;
}

/* form .input_block_section input:focus+.label {
  color: #1A73E8;
} */

form.mid-size {
  width: 35%;
}

.app .info_message {
  font-weight: 600;
  color: #283C62;
  font-size: 18px;
  text-align: center;
  margin-top: 10%;
}

.app .goback_button {
  color: #475773;
  font-weight: 500;
  font-size: 14px;
  display: flex;
  align-items: center;
  position: absolute;
  top: 11.5vh;
  cursor: pointer;
  animation: smoothContent 0.4s;
}

.app .back_icon {
  width: 8px;
  margin-right: 10px;
}

.app .button_new {
  position: fixed;
  right: 3%;
  bottom: 3%;
  background: white;
  padding: 1.5%;
  box-sizing: border-box;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.15);
  display: flex;
  transition: 0.2s;
  align-items: center;
  justify-content: center;
}

.app .button_new img {
  width: 22px;
  height: 22px;
}

.app .button_new:hover {
  padding: 1.7%;
}

.app .button_new:hover .app .button_new img {
  width: 28px;
  height: 28px;
}

.app .margin-top {
  width: 100%;
  height: 7vh;
}

.app .margin-top-small {
  width: 100%;
  height: 3vh;
}

.app .two-sides {
  display: flex;
  justify-content: space-between;
}

.app .left-page, .app .right-page {
  width: 45%;
}

.app .right-page.left-content {
  display: flex;
  justify-content: flex-start;
}

.app .right-page.right-content {
  display: flex;
  justify-content: flex-end;
}

.app .right-page.flex-center {
  display: flex;
  justify-content: center;
}

.app .error-screen {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: #FFFFFF;
  border: 1px solid #EEEEEE;
  box-sizing: border-box;
  border-radius: 26px;
  padding: 5% 0%;
  padding-top: 7%;
}

.app .error-screen img {
  max-width: 25%;
  max-height: 36vh;
}

.app .error-screen p {
  margin-top: 8vh;
  text-align: center;
  color: #192939;
  font-weight: 700;
}

.app .mobile-only {
  display: none;
}

.app input[type="date"]::-webkit-calendar-picker-indicator {
  color: rgba(0, 0, 0, 0);
  opacity: 1;
  display: block;
  background: url('/assets/svg/date.svg') no-repeat;
  width: 19px;
  height: 19px;
  border-width: thin;
  background-size: cover;
  outline: none;
  cursor: pointer;
  -moz-background-size: 100% 100%;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
  position: sticky;
  right: 0;
  /* -webkit-appearance: none; */
}

.app input[type="time"]::-webkit-calendar-picker-indicator {
  color: rgba(0, 0, 0, 0);
  opacity: 1;
  display: block;
  background: url('/assets/svg/time.svg') no-repeat;
  width: 16px;
  height: 16px;
  border-width: thin;
  background-size: cover;
  outline: none;
  cursor: pointer;
  -moz-background-size: 100% 100%;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
  position: sticky;
  right: 0;
  /* -webkit-appearance: none; */
}

/* Circle Colors */

.small_circle.blue {
  background: var(--task-color-blue);
}

.small_circle.green {
  background: var(--task-color-green);
}

.small_circle.red {
  background: var(--task-color-red);
}

.small_circle.pink {
  background: var(--task-color-pink);
}

.small_circle.yellow {
  background: var(--task-color-yellow);
}

/* Chrome, Safari, Edge, Opera */

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Icons */

.delete_icon:hover, .plus_icon:hover, .settings_block_icon:hover, .refresh_icon:hover, .file-line img:hover, .message-input button.send:hover, .edit_task_icon:hover, .app .back_icon:hover, .fc-icon:hover, .settings_icon:hover, .more_icon:hover, .grouppage .calendar-wrapper .fc .fc-today-button:hover, .app .goback_button:hover, .popup .close:hover, .popup .form_action .cancel:hover, .grouppage .school_name img:hover {
  opacity: 0.7;
  transition: 0.4s;
  cursor: pointer;
}

/* Firefox */

input[type=number] {
  -moz-appearance: textfield;
}

/* Animations */

@keyframes smooth {
  from {
    transform: scale(0.1);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes smoothContent {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes smoothPopup {
  from {
    transform: scale(0.8) translate(-60%, -55%);
    opacity: 0.5;
  }
  to {
    transform: scale(1) translate(-50%, -50%);
    opacity: 1;
  }
}

@keyframes goUp {
  from {
    transform: translateY(15vh);
  }
  to {
    transform: translateY(0px);
  }
}

@media (min-width: 1000px) and (max-width: 1024px) {
  .app .page {
    width: 93.5%;
  }
  .app .page_heading {
    font-size: 43px;
  }
  button.main {
    font-size: 14px;
    padding: 9px 21px;
    min-width: 125px;
  }
  .app .home .illustration-area {
    width: 30%;
  }
}

@media (min-width: 768px) and (max-width: 1000px) {
  .app .page_heading {
    font-size: 42px;
  }
  .app .page {
    width: 91%;
  }
  .app .goback_button {
    top: 8.5vh;
  }
  .app h3 {
    font-size: 28px;
  }
  button.main {
    font-size: 14px;
    padding: 8px 20px;
    min-width: 120px;
  }
  .app .home .illustration-area {
    width: 40%;
  }
}

@media (max-device-width: 480px) {
  .app .page {
    width: 100%;
    /* margin-bottom: 12vh; */
    min-height: 100vh;
  }
  .app .page_heading.space {
    margin-bottom: 5vh;
  }
  .app .page_content {
    padding: 4% 5%;
  }
  .app .home .illustration-area {
    width: 65%;
    margin-top: 12vh;
  }
  .app .home .illustration {
    /* width: 65%; */
    /* margin-top: 12vh; */
  }
  .app .home p {
    width: 90%;
    margin: 0 auto;
    font-weight: 600;
    font-size: 17px;
    line-height: 22px;
    text-align: center;
    /* color: #8C8C8C; */
    color: #151b26;
    margin-top: 10vh;
  }
  .app .page_heading {
    max-width: 100%;
    font-size: 35px;
    justify-content: space-between;
    margin-bottom: 11px;
    line-height: normal;
    margin-top: 4vh;
  }
  .app .goback_button {
    /* display: none; */
    top: calc(11.1vh + env(safe-area-inset-top));
  }
  .app .page_content .action-space {
    padding-top: 0;
  }
  .app .button_new {
    padding: 4%;
  }
  .app .button_new:hover {
    padding: 4.7%;
  }
  .app .left-page, .app .right-page {
    width: 100%;
  }
  .app .two-sides {
    flex-direction: column;
  }
  .app .mobile-only {
    display: block;
  }
  form .input_block_section input {
    width: 95%;
  }
  /* .app textarea{
    -webkit-appearance: none;
  } */
  .app input[type="date"], .app input[type="time"] {
    -webkit-appearance: none;
  }
  .app input[type="date"]:before {
    color: #B0B0B0;
    content: attr(placeholder);
  }
  .app input[type="time"]:before {
    color: #B0B0B0;
    content: attr(placeholder);
  }
  .app .error-screen {
    padding: 10%;
  }
  .app .error-screen p {
    font-size: 15px;
    color: #151B26;
    font-weight: 500;
    margin-top: 4vh;
  }
  .app .error-screen img {
    width: 70%;
    max-width: none;
    max-height: none;
  }
  input[type=checkbox] {
    /* -webkit-appearance: none; */
    -webkit-appearance: auto;
  }
  input[type=checkbox]:checked {
    background: var(--task-color-blue);
  }
  .app .home .magic-plus.mobile {
    display: block;
  }
  .app .home .magic-plus.pc {
    display: none;
  }
}