.leftmenu {
    width: 5%;
    height: 100vh;
    position: fixed;
    left: 0;
    /* top: 0; */
    bottom: 0;
    background: #151B26;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.09);
    transition: width 0.3s;
    z-index: 1;
    min-width: 60px;
}

.leftmenu .menu {
    display: block;
    margin: 0 auto;
    /* width: 40%; */
    /* margin-top: 30%; */
    cursor: pointer;
    max-width: 50px;
    width: 29px;
    margin-top: 3.5vh;
}

.leftmenu_bottom {
    position: absolute;
    bottom: 0;
    height: 10vh;
    background: white;
    width: 100%;
    background: linear-gradient(45deg, #4937E8 0%, #2A82E9 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 0.5s;
}

.leftmenu_bottom:hover {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)), linear-gradient(45deg, #4937E8 0%, #2A82E9 100%);
}

.leftmenu_bottom .plus {
    width: 35%;
    max-width: 45px;
}

.leftmenu .groups {
    /* display: flex;
    flex-direction: column;
    align-items: center; */
    margin-top: 5vh;
    height: 80.5vh;
    /* animation: smoothContent 0.7s; */
    overflow-x: auto;
}

.leftmenu .groups a {
    text-decoration: none;
}

.leftmenu .groups .menu_circle {
    background: white;
    color: #3761e8;
    width: 43px;
    height: 43px;
    display: block;
    text-align: center;
    /* padding: 2%; */
    font-size: 18px;
    font-weight: 600;
    border-radius: 50%;
    box-sizing: border-box;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-bottom: 3vh; */
    cursor: pointer;
    margin: 0 auto;
}

.leftmenu .groups .menu_circle img {
    width: inherit;
    height: inherit;
    border-radius: 50%;
    object-fit: cover;
    width: 100%;
    height: 100%;
    /* animation: smoothContent 0.6s; */
}

.leftmenu .groups .full_class_info {
    display: none;
}

.leftmenu .leftmenu_link {
    display: flex;
    align-items: center;
    justify-content: left;
    /* margin-bottom: 3vh; */
    width: 100%;
    box-sizing: border-box;
    padding: 2.2vh 0;
    /* padding: 3.2vh 0; */
}

.leftmenu .leftmenu_link.active {
    border-left: 6px solid #3e55e9;
    border-right: 6px solid transparent;
    background: #1E242F;
}

.leftmenu .leftmenu_top_line {
    display: none;
}

.leftmenu .leftmenu_bottom_opened {
    display: none;
}


/* Opened */

.leftmenu.opened {
    min-width: 300px;
    width: 23%;
}

.leftmenu.opened .menu {
    /* Menu Icon */
    display: none;
}

.leftmenu.opened .leftmenu_top_line {
    display: flex;
    /* width: 90%; */
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    height: 9vh;
    padding: 3% 5%;
    box-sizing: border-box;
    border-bottom: 1px solid #ffffff17;
}

.leftmenu.opened .groups {
    margin-top: 0;
}

.leftmenu.opened .leftmenu_link {
    padding: 2.2vh 4%;
    width: 100%;
    box-sizing: border-box;
    padding-left: 6%;
    /* animation: smoothContent 0.6s; */
}

.leftmenu.opened .leftmenu_link.active {
    /* border-left: 6px solid #3e55e9; */
    background: #1E242F;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.09);
    /* border-right: 6px solid transparent; */
}

.leftmenu.opened .leftmenu_link.active .menu_circle {
    margin-left: -6px;
}

.leftmenu.opened .leftmenu_link:first-child {
    /* border-top: 1px solid #ffffff17; */
}

.leftmenu.opened .leftmenu_link:last-child {
    /* border-bottom: 1px solid #ffffff17; */
}

.leftmenu.opened .groups .full_class_info {
    display: flex;
    flex-direction: column;
    margin-left: 6%;
}

.leftmenu.opened .groups .menu_circle {
    margin: unset;
    margin-bottom: 0;
    /* width: 40px;
    height: 40px;
    font-size: 15px; */
}

.leftmenu.opened .groups .full_class_info p {
    color: white;
    font-weight: 500;
    font-size: 19px;
    line-height: 0;
    opacity: 0.9;
    margin-bottom: 14px;
}

.leftmenu.opened .groups .full_class_info span {
    color: white;
    opacity: 0.6;
    font-weight: 400;
    font-size: 14px;
    /* line-height: 11px; */
}

.leftmenu.opened .groups {
    height: 80.8vh;
}

.leftmenu.opened .menu_logo_block {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.leftmenu.opened .menu_logo {
    /* width: 28px; */
    height: 5.4vh;
    max-height: 41px;
}

.leftmenu.opened .menu_logo_text {
    color: white;
    font-weight: bold;
    font-size: 20px;
    margin-left: 10%;
}

.leftmenu.opened .menu_close {
    width: 14%;
    max-width: 42px;
    cursor: pointer;
}

.leftmenu.opened .leftmenu_bottom {
    display: none;
}

.leftmenu.opened .leftmenu_bottom_opened {
    display: flex;
    position: absolute;
    width: 100%;
    bottom: 0;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #ffffff17;
}

.leftmenu.opened .leftmenu_bottom_opened_text {
    width: 75%;
    text-align: center;
}

.leftmenu.opened .leftmenu_bottom_opened p {
    color: #FFFFFF;
    opacity: 0.8;
}

.leftmenu.opened .leftmenu_plus_square {
    bottom: 0;
    height: 10vh;
    background: white;
    width: 25%;
    background: linear-gradient(45deg, #4937E8 0%, #2A82E9 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

@media (min-width: 1000px) and (max-width: 1024px) {
    .leftmenu {
        width: 6.5%;
    }
    .leftmenu.opened {
        width: 30%;
    }
}

@media (min-width: 768px) and (max-width: 1000px) {
    .leftmenu {
        width: 9%;
    }
    .leftmenu.opened {
        width: 44%;
    }
}

@media (max-device-width: 480px) {
    .leftmenu {
        opacity: 0;
        /* visibility: hidden; */
        transition: 0.2s;
        pointer-events: none;
        padding-top: env(safe-area-inset-top);
        /* display: none; */
    }
    .leftmenu.opened {
        pointer-events: unset;
        /* display: block; */
        opacity: 1;
        /* visibility: visible; */
        width: 80%;
        top: 0;
        /* bottom: 0; */
        /* padding-bottom: env(safe-area-inset-bottom); */
        /* padding-top: env(safe-area-inset-top);   */
    }
    .leftmenu.opened .leftmenu_top_line {
        /* padding-top: env(safe-area-inset-top);  */
        /* height: calc(9vh + env(safe-area-inset-top)); */
    }
    .leftmenu .groups {
        display: block;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }
    .leftmenu.opened .groups {
        height: calc(80vh - env(safe-area-inset-top));
    }
    .leftmenu.opened .leftmenu_bottom_opened {
        /* padding-bottom: 1.36vh; */
        position: fixed;
        bottom: 0;
        width: 80%;
        background: #151b26;
        min-width: 300px;
        /* padding-bottom: env(safe-area-inset-bottom); */
    }
    .leftmenu.opened .leftmenu_plus_square {
        /* padding-bottom: env(safe-area-inset-bottom); */
    }
    .leftmenu.opened .leftmenu_bottom_opened_text {
        /* padding-bottom: env(safe-area-inset-bottom); */
    }
    .leftmenu .groups .menu_circle img {
        animation: smoothContent 0.6s;
    }
    .leftmenu.opened .menu_logo_text {
        font-size: 18px;
    }
    .leftmenu.opened .menu_logo {
        width: 20px;
    }
    .leftmenu.opened .leftmenu_top_line {
        /* padding: 0 0; */
    }
    .leftmenu.opened .menu_close {
        width: 12.5%;
    }
}