.grouppage .school_name {
    font-weight: 500;
    font-size: 21px;
    line-height: 30px;
    color: #151B26;
    margin-bottom: 7vh;
    display: flex;
    align-items: center;
}

.grouppage .school_name img {
    width: 45px;
    height: 45px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 15px;
}

.grouppage .settings_icon {
    width: 30px;
    margin-left: 20px;
    cursor: pointer;
}

.grouppage .more_icon {
    width: 6px;
    margin-left: 3%;
    cursor: pointer;
}

.grouppage .forward_icon {
    width: 10px;
    margin-left: 14px;
    cursor: pointer;
}

.grouppage .clock {
    font-weight: 100;
    color: #3170E9;
    font-size: 50px;
    float: right;
    padding-top: 10px;
}

.grouppage .grouppage_blocks {
    display: grid;
    grid-template-columns: 63% 33%;
    margin-top: 3%;
    grid-gap: 4%;
}

.grouppage .grouppage_blocks.second {
    grid-template-columns: 26.5% 32.5% 33%;
}

.grouppage .grouppage_blocks .block {
    position: relative;
}

.grouppage .grouppage_blocks .block .plus_icon {
    width: 22px;
    /* margin-left: 3%; */
    margin-left: 14px;
    cursor: pointer;
}

.grouppage .grouppage_blocks .block .refresh_icon {
    width: 15px;
    /* margin-left: 3%; */
    margin-left: 14px;
    cursor: pointer;
}

.grouppage .icon_link {
    display: flex;
    text-decoration: none;
}

.grouppage .grouppage_blocks .block .settings_block_icon {
    width: 22px;
    margin-left: 8px;
    cursor: pointer;
}

.grouppage .grouppage_blocks .block_heading {
    display: flex;
    align-items: center;
    font-size: 19px;
    margin-bottom: 15px;
    font-weight: 500;
    color: #151B26;
}

.grouppage .grouppage_blocks .block_heading a{
    color: #151B26;
    text-decoration: none;
}

.grouppage .widget_box {
    min-height: 40vh;
    background: white;
    width: 100%;
    border: 1px solid #EEEEEE;
    box-sizing: border-box;
    border-radius: 26px;
    position: relative;
}

.grouppage .block .bottom-gradient {
    width: 99.7%;
    height: 48px;
    position: sticky;
    bottom: -1px;
    left: 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 63.85%, rgb(255 255 255 / 98%) 100%);
    border-radius: 0 0 26px 26px;
    /* background: red; */
}

.grouppage .widget_box.calendar {
    box-sizing: border-box;
    padding: 7%;
    min-height: 48vh;
}

.grouppage .widget_box .tasks_slider {
    overflow: auto;
    /* white-space: nowrap; */
    display: flex;
    align-items: center;
    padding: 4% 0%;
    box-sizing: border-box;
}

.grouppage .widget_box .tasks_slider, .grouppage .widget_box .tasks_slider+.gradient {
    height: 45vh;
}

.grouppage .widget_box .tasks_slider+.gradient {
    border-radius: 26px;
    width: 10%;
    position: absolute;
    right: 0;
    top: 0;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.95) 97.4%);
}

.grouppage .widget_box .tasks_slider .task_slide {
    display: flex;
    box-sizing: border-box;
    padding: 0% 4%;
    border-right: 1px solid #E5E5E5;
    min-width: 30%;
    width: 30%;
    flex-direction: column;
    position: relative;
    height: 100%;
}

.grouppage .widget_box .tasks_slider .task_heading {
    font-weight: 500;
    font-size: 18px;
    color: #151B26;
    margin-bottom: 2vh;
    display: flex;
    /* align-items: center; */
    align-items: baseline;
}

.grouppage .widget_box .tasks_slider .task_circle {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    margin-right: 9px;
    display: inline-table;
}

.grouppage .widget_box .tasks_slider .task_deadline {
    font-weight: 500;
    font-size: 13px;
    color: #3170E9;
    margin-bottom: 3vh;
}

.grouppage .widget_box .tasks_slider .task_short_description {
    font-size: 12px;
    line-height: 17px;
    color: #475773;
    font-weight: 400;
    word-break: break-word;
    /* margin-bottom: 11vh; */
}

.grouppage .widget_box .tasks_slider .task_more {
    position: absolute;
    bottom: 0;
    padding: 8px 24px;
    width: 73%;
}

.grouppage .widget_box .tasks_slider .task_more.mobile {
    display: none;
}

.grouppage .widget_box.chat {
    box-sizing: border-box;
    padding: 7%;
    min-height: 54vh;
}

.grouppage .widget_box .widget_info {
    width: 100%;
    text-align: center;
    color: #686868;
    font-weight: bold;
    font-size: 16px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: inherit;
}

.grouppage .widget_box .widget_info span {
    margin-top: 18px;
    /* max-width: 70%; */
    max-width: 90%;
    font-size: 15px;
    color: #151B26;
    font-weight: 500;
}

.grouppage .widget_box .rating-list {
    box-sizing: border-box;
    padding: 5% 0;
}

.grouppage .widget_box .rating-list .rating-line {
    display: flex;
    align-items: center;
    justify-content: unset;
    border-bottom: 1px solid #E5E5E5;
    box-sizing: border-box;
    width: 85%;
    margin: 0 auto;
    padding: 5% 0;
}

.grouppage .widget_box .rating-list .rating-line img {
    width: 38px;
    height: 38px;
    border-radius: 50%;
    /* margin-left: 8%; */
    margin-right: 5%;
    object-fit: cover;
}

.grouppage .widget_box .rating-list .rating-line span.place {
    color: #3170E9;
    font-weight: 500;
    font-size: 18px;
    margin-right: 8%;
    min-width: 4%;
}

.grouppage .widget_box .rating-list .rating-line span.name {
    font-size: 16px;
    color: #252F42;
    font-weight: 500;
}

.grouppage .widget_box .rating-list .rating-line span.rating {}

.grouppage .widget_box.bottom_scroll_box {
    height: 48vh;
    overflow-x: auto
}

.grouppage .widget_box .files-list {
    box-sizing: border-box;
    padding: 8.4% 7%;
}

.grouppage .widget_box .files-list .file-line {
    margin-bottom: 3vh;
    border-bottom: 1px solid #EEEEEE;
    padding-bottom: 3vh;
}

.grouppage .widget_box .files-list .file-line a {
    display: flex;
}

.grouppage .widget_box .files-list .file-line .top-line {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.grouppage .widget_box .files-list .file-line img {
    width: 30px;
    height: 30px;
    cursor: pointer;
}

.grouppage .widget_box .files-list .file-line span.name {
    font-weight: 500;
    color: #252F42;
    font-size: 16px;
    /* margin-bottom: 1vh; */
    width: 54%;
}

.grouppage .widget_box .files-list .file-line span.type {
    color: #3170E9;
    font-size: 13px;
    text-align: right;
    width: 30%;
}

.grouppage .widget_box .files-list .file-line span.comment {
    color: #475773;
    font-size: 14px;
    margin-top: 1vh;
    display: block;
}

.grouppage .groupmenu_clicker_overlay {
    position: fixed;
    width: 90%;
    height: 80vh;
    z-index: 10;
    /* background: #d3d3d369; */
}

.grouppage .widget_box .chat .message-input{
    width: 100%;
    background: white;
    box-shadow: 0px -1px 4px rgba(0, 0, 0, 0.08);
    box-sizing: border-box;
    padding: 4% 0%;
    bottom: 0;
    position: absolute;
    left: 0;
    border-radius: 0 0 26px 26px;
}

.grouppage .widget_box .chat .message-input .wrapper{
    position: relative;
    width: 93%;
    margin: 0 auto;
}

.grouppage .widget_box .chat .message-input input{
    width: 100%;
    box-sizing: border-box;
    padding: 11px 16px;
    border: 1px solid #E5E5E5;
    border-radius: 10px;
    color: #151515;
    font-size: 14px;
    padding-right: 38px;
}

.grouppage .widget_box .chat .message-input img.send{
    position: absolute;
    width: 30px;
    height: 30px;
    right: 6px;
    top: 6px;
    cursor: pointer;
}

.grouppage .widget_box .chat .message-input button.send{
    background: url(/assets/svg/send-message.svg);
    background-size: contain;
    border: 0;
    position: absolute;
    width: 30px;
    height: 30px;
    right: 6px;
    top: 6px;
    cursor: pointer;
    background-repeat: no-repeat;
}

.grouppage .chat-messages-list{
    /* height: 37vh; */
    height: 38vh;
    overflow-y: auto;
    display: flex;
    flex-direction: column-reverse;
    padding-bottom: 7vh;
}

.grouppage .chat .chat-message{
    display: flex;
    margin-top: 1.5vh;
    color: #151515;
}

.grouppage .chat .chat-message.me{
    margin-top: 0.9vh;
}

.grouppage .chat .chat-message img.user-picture{
    width: 38px;
    height: 38px;
    object-fit: cover;
    border-radius: 50px;
}

.grouppage .chat .chat-message .message-wrapper{
    border: 1px solid #EEEEEE;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 10px 15px;
    margin-left: 10px;
    max-width: 100%;
}

.grouppage .chat .chat-message .message-wrapper.me{
    margin-left: 48px;
}

.grouppage .chat .chat-message span.name{
    font-size: 12px;
    font-weight: 500;
}

.grouppage .chat .chat-message .message{
    font-weight: 300;
    font-size: 15px;
    margin-top: 2px;
    max-width: 100%;
    word-break: break-word;
}

.grouppage .chat .chat-message .message a{
    color: #3170E9;
}

.grouppage .chat .chat-message .message iframe{
    width: 100%;
    height: 185px;
}

.grouppage .chat .chat-message .message img{
    width: 100%;
    max-height: 400px;
} 

.grouppage .chat .load-more{
    /* text-align: center;
    color: #3170E9;
    font-size: 14px;
    margin-bottom: 3vh;
    cursor: pointer;
    text-decoration: underline; */
    text-align: center;
    color: white;
    font-size: 12px;
    cursor: pointer;
    background: linear-gradient(45deg, #4937E8 0%, #2A82E9 100%);
    border-radius: 100px;
    box-sizing: border-box;
    display: inline-block;
    padding: 9px 12px;
    width: 62%;
    margin: 0 auto;
    margin-bottom: 3vh;
}

.grouppage .group-menu {
    background: #FFFFFF;
    border: 1px solid #EEEEEE;
    box-sizing: border-box;
    box-shadow: 0px 5px 7px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    z-index: 11;
    position: absolute;
    right: 0;
    padding: 8px 10px;
    font-weight: 400;
    color: #151B26;
    font-size: 15px;
    line-height: normal;
    width: 120px;
    justify-content: center;
    display: flex;
    cursor: pointer;
    animation: smoothContent 0.2s;
    transition: 0.3s;
}

.grouppage .group-menu:hover {
    background: #ffffff8a;
}

.grouppage .block .event-info {
    width: 100%;
    height: 43vh;
    background: white;
    position: absolute;
    z-index: 10;
    bottom: 0;
    left: 0;
    box-shadow: 0px -1px 3px rgb(0 0 0 / 0.06);
    border-radius: 26px;
    box-sizing: border-box;
    padding: 7%;
    animation: goUp 0.7s;
}

.grouppage .block .event-info .date {
    color: #151B26;
    font-size: 14px;
    box-sizing: border-box;
    /* border-bottom: 1px solid #E5E5E5; */
    /* padding: 2vh 0; */
    /* margin-bottom: 2vh; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
}

.grouppage .block .event-info .date .blue {
    /* color: #3170E9;
    font-weight: 400;
    font-size: 19px; */
}

.grouppage .block .event-info .top-line {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid #E5E5E5;
    padding-bottom: 3vh;
    margin-bottom: 3vh;
}

.grouppage .block .event-info .name {
    display: flex;
    color: #151B26;
    font-weight: 500;
    font-size: 17px;
    align-items: center;
    /* border-bottom: 1px solid #E5E5E5;
    padding: 2vh 0;
    margin-bottom: 2vh; */
}

.grouppage .block .event-info .hours {
    font-size: 17px;
    font-weight: 400;
    color: #3170E9;
    margin-top: 2vh;
    display: block;
}

.grouppage .block .event-info .circle {
    background: #3170E9;
    width: 10px;
    height: 10px;
    border-radius: 50px;
    margin-right: 10px;
}

.grouppage .block .event-info .event-info-close {
    /* position: absolute;
    top: 20px;
    right: 20px; */
    cursor: pointer;
    width: 14px;
    height: 14px;
}

.grouppage .block .event-info p {
    color: #475773;
    font-size: 14px;
    line-height: 22px;
    overflow-x: auto;
    height: 15.5vh;
    padding-bottom: 3vh;
    box-sizing: border-box;
}

.grouppage .block .event-info .bottom-gradient {
    border-radius: 0;
}

.grouppage .block .event-info .actions {
    position: absolute;
    bottom: 5%;
    display: flex;
    align-items: center;
}

.grouppage .block .event-description {
    position: relative;
}

.grouppage .block .event-info button.main {
    padding: 8px 23px;
    font-size: 13px;
}

.grouppage .block .event-info .copy-link {
    font-weight: 400;
    text-decoration-line: underline;
    color: #3170E9;
    font-size: 14px;
    display: flex;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;
}

.grouppage .block .event-info .copy-link img {
    margin-right: 8px;
    width: 20px;
}

.grouppage .calendar-wrapper {}

.grouppage .calendar-wrapper .fc .fc-button {
    padding: 0 0.20em;
}

.grouppage .calendar-wrapper .fc .fc-today-button {
