.grouppage .calendar-wrapper .fc-direction-ltr .fc-list-day-side-text, .fc-direction-rtl .fc-list-day-text, .grouppage .calendar-wrapper .fc-direction-ltr .fc-list-day-text, .fc-direction-rtl .fc-list-day-side-text {
