    display: flex;
    flex-direction: column;
    height: 47vh;
    width: 100%;
}

.grouppage .info-message .info-illustration{
    width: 180px;
    height: 21vh;
}

.grouppage .info-message span{
    margin-top: 3vh!important;
}

