        height: 41vh;
    }
    .grouppage .widget_box.calendar {
        min-height: 44vh;
    }
    .grouppage .widget_box.chat {
        min-height: 50vh;
    }
    .grouppage .chat-messages-list{
        height: 31vh;
    }
    .grouppage .widget_box .tasks_slider .task_more {
        font-size: 11px;
        padding: 7px 20px;
